<template>
    <el-container>
        <el-header>
            <container-top></container-top>
        </el-header>
        <el-container>
            <nav-header></nav-header>
            <el-main>
                <el-container class="container-main-div">
                    <div class="container-div">
                        <div class="container-main">
                            <div class="top-main">
                                <div class="page-top-line">
                                    <div class="page-title">账户管理</div>
                                </div>
                                <div class="screen-block">
                                    <div>
                                        <el-radio v-model="screenSelect" label="1">身份</el-radio>
                                        <el-select v-model="inputIdentity" class="screen-input" placeholder="请选择身份">
                                            <el-option
                                                v-for="item in identityList"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                            </el-option>
                                        </el-select>
                                        <el-radio v-model="screenSelect" label="2">账号</el-radio>
                                        <el-input v-model="inputAccount" class="screen-input" placeholder="请输入账号"></el-input>
                                        <el-radio v-model="screenSelect" label="3">全部</el-radio>
                                        <el-button @click="search" class="start-search">搜索</el-button>
                                    </div>
                                    <el-button class="create-account" @click="handleAdd">创建账号</el-button>
                                </div>
                            </div>
                            <div class="out-div">
                                <div class="account-list">
                                    <template>
                                        <el-table :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%">
                                            <el-table-column prop="id" label="序号" width="80">
                                                <template slot-scope="scope">
                                                    <span>{{(currentPage - 1) * pagesize + scope.$index + 1}}</span>
                                                </template>
                                            </el-table-column>
                                            <el-table-column prop="userName" label="用户姓名"></el-table-column>
                                            <el-table-column prop="userAccount" label="账号"></el-table-column>
                                            <el-table-column prop="userType" label="身份"></el-table-column>
                                            <el-table-column prop="institution" label="所属机构"></el-table-column>
                                            <el-table-column label="操作" width="200px">
                                                <template slot-scope="scope">
                                                    <div class="cell">
                                                        <div @click="handleEdit(scope.$index, scope.row)" class="btns edit-btn" v-if="scope.row.userType!='平台管理员'||scope.row.userName==username">
                                                            <img src="static/images/edit.png" alt="">
                                                            <p>修改</p>
                                                        </div>
                                                        <div @click="handleDelete(scope.$index, scope.row)" class="btns del-btn" v-if="scope.row.userType!='平台管理员'||scope.row.userName==username">
                                                            <img src="static/images/del.png" alt="">
                                                            <p>删除</p>
                                                        </div>
                                                    </div>
                                                </template>
                                            </el-table-column>
                                        </el-table>
                                        <el-pagination
                                            @size-change="handleSizeChange"
                                            @current-change="handleCurrentChange"
                                            :current-page="currentPage"
                                            :page-sizes="[5,10,20,50]"
                                            :page-size="pagesize"
                                            layout="total, sizes, prev, pager, next, jumper"
                                            :total="tableData.length"
                                            style="margin-top:15px">
                                        </el-pagination>
                                    </template>
                                </div>
                            </div>
                        </div>
                    </div>
                </el-container>
                <el-dialog title="创建账户" :visible.sync="dialogVisibleAdd" width="400px" :lock-scroll="false" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
                    <el-form ref="formAdd" :rules="rulesAdd" :model="formAdd" label-width="80px">
                        <el-form-item label="员工姓名" prop="userName">
                            <el-input v-model="formAdd.userName"></el-input>
                        </el-form-item>
                        <el-form-item label="账号" prop="userAccount">
                            <el-input v-model="formAdd.userAccount"></el-input>
                        </el-form-item>
                        <el-form-item label="密码设置" prop="password">
                            <el-input v-model="formAdd.password"></el-input>
                        </el-form-item>
                        <el-form-item label="所属机构" prop="institution">
                            <el-select v-model="formAdd.institution" placeholder="请选择所属机构">
                                <el-option
                                    v-for="item in userTypeList"
                                    :key="item.nodeName"
                                    :label="item.nodeName"
                                    :value="item.nodeName">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="身份设置" prop="userType">
                            <el-radio-group v-model="formAdd.userType">
                                <el-radio :label="100" v-model="form.userType">节点管理员</el-radio>
                                <el-radio :label="200" v-model="form.userType">节点普通用户</el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button class="submit-btn-style" type="primary" @click="addAccount('formAdd')">确 定</el-button>
                        <el-button class="cancle-btn-style" @click="dialogVisibleAdd = false">取 消</el-button>
                    </span>
                </el-dialog>
                <el-dialog title="修改账号" :visible.sync="dialogVisibleEdit" width="400px" :lock-scroll="false" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
                    <el-form ref="form" :rules="rulesEdit" :model="form" label-width="80px">
                        <el-form-item label="ID" style="display:none">
                            <el-input v-model="form.id"></el-input>
                        </el-form-item>
                        <el-form-item label="员工姓名" prop="userName">
                            <el-input v-model="form.userName"></el-input>
                        </el-form-item>
                        <el-form-item label="账号" prop="userAccount">
                            <el-input v-model="form.userAccount" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="密码设置" prop="password">
                            <el-input v-model="form.password"></el-input>
                        </el-form-item>
                        <el-form-item label="所属机构" prop="institution">
                            <el-select v-model="form.institution" placeholder="请选择所属机构">
                                <el-option
                                    v-for="item in userTypeList"
                                    :key="item.nodeName"
                                    :label="item.nodeName"
                                    :value="item.nodeName">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="身份设置" prop="userType">
                            <el-radio-group v-model="form.userType">
                                <el-radio :label="100" v-model="form.userType">节点管理员</el-radio>
                                <el-radio :label="200" v-model="form.userType">节点普通用户</el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button class="submit-btn-style" type="primary" @click="editAccount('form')">确 定</el-button>
                        <el-button class="cancle-btn-style" @click="dialogVisibleEdit = false">取 消</el-button>
                    </span>
                </el-dialog>
                <el-dialog title="" :visible.sync="dialogVisibleDel" width="400px" :lock-scroll="false" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
                    <img src="static/images/del_tip.png" class="del-tip-style" alt="">
                    <p class="del-tip-p">确定删除？</p>
                    <el-form ref="formDel" :model="formDel" label-width="80px">
                        <el-form-item label="ID" style="display:none">
                            <el-input v-model="formDel.userAccount"></el-input>
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button class="submit-btn-style" type="primary" @click="deleteThis">确 定</el-button>
                        <el-button class="cancle-btn-style" @click="dialogVisibleDel = false">取 消</el-button>
                    </span>
                </el-dialog>
            </el-main>
        </el-container>
    </el-container>
</template>
<style>
    @import "../../static/css/account.css";
</style>
<script>
import $ from "jquery";
import Header from "../sub/aside";
import containerTop from "../sub/container_top";
export default {
    name: "homepage",
    components: {
        "nav-header": Header,
        "container-top": containerTop,
    },
    data: function() {
        return {
            username:Cookies.get("fedname"),
            token:Cookies.get("fedtoken"),
            screenSelect:'1',
            inputIdentity:"节点管理员",
            inputAccount:"",
            identityList:[{
                "value":"节点管理员",
                "label":"节点管理员"
            }, {
                "value":"节点普通用户",
                "label":"节点普通用户"
            }],
            tableData: [],
            pagesize:10,
            currentPage:1,
            // 创建账号弹窗
            dialogVisibleAdd:false,
            formAdd:{},
            rulesAdd:{
                userName:[{ required: true, message: '请填写姓名', trigger: 'blur' }],
                userAccount:[{ required: true, message: '请填写账号', trigger: 'blur' }],
                password:[{ required: true, message: '请填写密码', trigger: 'blur' }],
                institution:[{ required: true, message: '请选择所属机构', trigger: 'change' }],
                userType:[{ required: true, message: '请选择身份', trigger: 'change' }],
            },
            userTypeList:[],
            // 修改弹窗
            dialogVisibleEdit:false,
            form:{},
            rulesEdit:{
                userName:[{ required: true, message: '请填写姓名', trigger: 'blur' }],
                userAccount:[{ required: true, message: '请填写账号', trigger: 'blur' }],
                password:[{ required: true, message: '请填写密码', trigger: 'blur' }],
                institution:[{ required: true, message: '请选择所属机构', trigger: 'change' }],
                userType:[{ required: true, message: '请选择身份', trigger: 'change' }],
            },
            // 删除弹窗
            dialogVisibleDel:false,
            formDel:{},
        }
    },
    mounted(){
        this.findDimensions()
        this.getAccountList()
        this.getNodeList()
    },
    methods: {
        // 账号列表
        getAccountList:function(){
            var that = this;
            that.axios.post('/userReq',that.qs.stringify({
                "askUserType": '',
                "askUserAccount": '',
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    that.tableData=response.data.users
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },
        // 创建账号
        handleAdd:function(){
            var that=this;
            that.dialogVisibleAdd=true;
        },
        addAccount:function(form){
            var that=this;
            that.$refs[form].validate((valid) => {
                if (valid) {
                    that.dialogVisibleAdd=false;
                    that.axios.post('/userCreate',that.qs.stringify({
                        'userName':that.formAdd.userName,
                        'userAccount':that.formAdd.userAccount,
                        'password':that.formAdd.password,
                        'userType':that.formAdd.userType==100?"节点管理员":"节点普通用户",
                        'institution':that.formAdd.institution,
                        "operator":that.username,
                        "token":that.token
                    }),
                    {
                        headers:{
                            'Content-Type':'application/x-www-form-urlencoded',
                        }
                    },
                    {
                        emulateJSON:true
                    })
                    .then(function(response){
                        if(response.data.message.state){
                            that.$message({
                                message: '创建账号成功',
                                type: 'success'
                            });
                            that.currentPage=1;
                            that.formAdd={}
                            that.getAccountList();
                        }
                        else {
                            that.$message.error(response.data.message);
                        }
                    },function(error){
                        that.$message.error(error.response.data.message);
                        if(error.response.status==401){
                            that.$router.push('/');
                            Cookies.set("fedname",1,{expires:-1});
                            Cookies.set("fedrole",1,{expires:-1});
                        }
                    })
                }
                else {
                    return false;
                }
            });
        },
        // 修改
        handleEdit:function(index, rowData){
            var that=this;
            that.dialogVisibleEdit = true;
            that.form = Object.assign({}, rowData);
            if(rowData.userType=="节点管理员"){
                that.form.userType=100
            }
            else {
                that.form.userType=200
            }
        },
        editAccount:function(form){
            var that=this;
            that.$refs[form].validate((valid) => {
                if (valid) {
                    that.dialogVisibleEdit=false;
                    that.axios.post('/userModify',that.qs.stringify({
                        'userName':that.form.userName,
                        'userAccount':that.form.userAccount,
                        'password':that.form.password,
                        'userType':that.form.userType==100?"节点管理员":"节点普通用户",
                        'institution':that.form.institution,
                        "operator":that.username,
                        "token":that.token
                    }),
                    {
                        headers:{
                            'Content-Type':'application/x-www-form-urlencoded',
                        }
                    },
                    {
                        emulateJSON:true
                    })
                    .then(function(response){
                        if(response.data.message.state){
                            that.$message({
                                message: '修改成功',
                                type: 'success'
                            });
                            that.currentPage=1;
                            that.getAccountList()
                        }
                        else {
                            that.$message.error(response.data.message);
                        }
                    },function(error){
                        that.$message.error(error.response.data.message);
                        if(error.response.status==401){
                            that.$router.push('/');
                            Cookies.set("fedname",1,{expires:-1});
                            Cookies.set("fedrole",1,{expires:-1});
                        }
                    })
                }
                else {
                    return false;
                }
            });
        },
        // 删除
        handleDelete(index, rowData) {
            this.dialogVisibleDel = true;
            this.formDel= Object.assign({}, rowData);
        },
        deleteThis:function(){
            var that=this;
            that.dialogVisibleDel=false;
            that.axios.post('/userDelete',that.qs.stringify({
                'userAccount':that.formDel.userAccount,
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    that.$message({
                        message: '删除账号成功',
                        type: 'success'
                    });
                    that.currentPage=1;
                    that.getAccountList()
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },

        // 节点列表
        getNodeList:function(){
            var that = this;
            that.axios.post('/nodeReq',that.qs.stringify({
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    that.userTypeList=response.data.nodes
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },

        // 搜索
        search:function(){
            var that = this;
            if(that.screenSelect==1){
                that.axios.post('/userReq',that.qs.stringify({
                    "askUserType": that.inputIdentity,
                    "askUserAccount": '',
                    "operator":that.username,
                    "token":that.token
                }),
                {
                    headers:{
                        'Content-Type':'application/x-www-form-urlencoded',
                    }
                },
                {
                    emulateJSON:true
                })
                .then(function(response){
                    if(response.data.message.state){
                        that.$message({
                            message: '搜索成功，以下为搜索结果',
                            type: 'success'
                        });
                        that.tableData=response.data.users
                    }
                    else {
                        that.$message.error(response.data.message);
                    }
                },function(error){
                    that.$message.error(error.response.data.message);
                    if(error.response.status==401){
                        that.$router.push('/');
                        Cookies.set("fedname",1,{expires:-1});
                        Cookies.set("fedrole",1,{expires:-1});
                    }
                })
            }
            else if(that.screenSelect==2){
                if(that.inputAccount==""){
                    this.$message.error('请输入需要搜索的账号');
                }
                else{
                    that.axios.post('/userReq',that.qs.stringify({
                        "askUserType": '',
                        "askUserAccount": that.inputAccount,
                        "operator":that.username,
                        "token":that.token
                    }),
                    {
                        headers:{
                            'Content-Type':'application/x-www-form-urlencoded',
                        }
                    },
                    {
                        emulateJSON:true
                    })
                    .then(function(response){
                        if(response.data.message.state){
                            that.$message({
                                message: '搜索成功，以下为搜索结果',
                                type: 'success'
                            });
                            that.tableData=response.data.users
                        }
                        else {
                            that.$message.error(response.data.message);
                        }
                    },function(error){
                        that.$message.error(error.response.data.message);
                        if(error.response.status==401){
                            that.$router.push('/');
                            Cookies.set("fedname",1,{expires:-1});
                            Cookies.set("fedrole",1,{expires:-1});
                        }
                    })
                }
            }
            else if(that.screenSelect==3){
                that.getAccountList()
            }
        },
        handleSizeChange: function (size) {
            this.pagesize = size;
        },
        handleCurrentChange: function(currentPage){
            this.currentPage = currentPage;
        },
        findDimensions:function(){
            if(window.innerWidth)
            this.winWidth = window.innerWidth;
            else if ((document.body) && (document.body.clientWidth))
            this.winWidth = document.body.clientWidth;
            //获取窗口高度
            if(window.innerHeight)
            this.winHeight = window.innerHeight;
            else if((document.body) && (document.body.clientHeight))
            this.winHeight = document.body.clientHeight;
            //通过深入Document内部对body进行检测，获取窗口大小
            if(document.documentElement && document.documentElement.clientHeight && document.documentElement.clientWidth){
                this.winHeight = document.documentElement.clientHeight;
                this.winWidth = document.documentElement.clientWidth;
            }
            $(".account-list").attr("style","min-height:"+(this.winHeight-360)+"px;")
        },
    }
};
</script>



